
import { parse } from 'query-string';
import React, { useState, useEffect } from 'react';
import { useLocation, useParams } from 'react-router-dom';
import { requesicoes } from '../../../utils/services';
import '../pauta/pautaCadeira.scss';
import '../pauta/pauta.css';
import { Alert, Button, TextField } from '@mui/material';
import SaveIcon from '@material-ui/icons/Save';
import { makeStyles, } from '@material-ui/core/styles';
import Swal from 'sweetalert2';
import { Redirect } from 'react-router-dom';

let estudantes = [];
let cadeira = '';
let testes = [];
let avaliacoes = [];
let avaliacoesExame = []
let notadeRecore = ''
let notadeExame = ''
let semNotasdeExames = ''


const useStyles = makeStyles((theme) => ({
    root: {
        '&:hover': {
            backgroundColor: 'transparent',
        },
    },

}));

const PautaCadeira = () => {

    const location = useLocation();
    const idCadeira = atob(parse(location.search).cadeira);
    const idCurso = atob(parse(location.search).curso);
    const turma = atob(parse(location.search).turma);
    const [to, setTo] = useState('');
    const [redirect, setRedirect] = useState(false);
    const [notasModificadas, setNotasModificadas] = useState([]);
    const [frequencias, setFrequencias] = useState({});
    const [updateDom, setUpdateDom] = useState(false);
    const [notas, setNotas] = useState([]);
    const [carregando, setCarregando] = useState(true);

    const classes = useStyles();

    const parametros = useParams();

    let id = window.location.href.split('/');
    id = (id[id.length - 1]);

    useEffect(() => {
        init();
    }, [])


    async function init() {
        setCarregando(true);

        const [avaliacoesRequest, avaliacaoExameRequest, listaEstudantes, testeRequest] = await Promise.all([
            requesicoes().getAvaliacoesByCadeiraAndTurma(`/${idCadeira}/${turma}?campos=id,tipoAvaliacao,ordem,cadeira.id,cadeira.nome,turma.id,turma.turma`),
            requesicoes().getAvaliacoesExameByCadeiraAndTurma(`/${idCadeira}/${turma}?campos=id,tipoAvaliacao,ordem,cadeira.id,cadeira.nome,turma.id,turma.turma`),
            requesicoes().getEstudanteTurma(`/${turma}/${idCadeira}?campos=nome,id,apelido,numero,matricula.id,matricula.anoFrequencia,matricula.turma,curso.id,curso.nome`),
            requesicoes().getTesteByCurso(`/${idCurso}/${idCadeira}?campos=id,nota,avaliacao,estudante.id,estudante.nome,estudante.apelido,estudante.numero`)]);//estudante.matricula

        avaliacoesExame = avaliacaoExameRequest.data;
        estudantes = listaEstudantes.data;
        testes = testeRequest.data;
        avaliacoes = avaliacoesRequest.data;
        console.log(estudantes,'estudantes');

console.log(testes,'testes');
        if (testes.length === 0) {
            testes = estudantes;

        }
        // Verifique se algum elemento é do tipo "exame"
        const hasExame = avaliacoesExame.some(avaliacao => avaliacao.tipoAvaliacao === "Exame")
        const hasNotExame = avaliacoes.some(avaliacao => avaliacao.tipoAvaliacao === "Exame")


        if (!avaliacoesExame || avaliacoesExame.length === 0) {

            const values = [{ "exame": "Exame", "cadeira": { id: idCadeira }, "turma": { id: turma } },
            { "cadeira": { id: idCadeira }, "turma": { id: turma }, "rec": "Rec" }]
            try {
                await Promise.resolve(requesicoes().postAvalicoes(values));
                init();
            } catch (e) {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Ocorreu um erro!',
                    footer: 'Se o erro persistir entre em contacto com o seu Departamento'
                }).then(() => {
                    setTo('/teacher/exame');
                    setRedirect(true);
                });
                setTo('/teacher/exame');
                setRedirect(true);
            }

            testes = []
        }

        const avaliacoesNormais = avaliacoes.filter(avaliacao => avaliacao.tipoAvaliacao !== "Exame" && avaliacao.tipoAvaliacao !== "Rec");

        const testesPorEstudante = {};
        testes.forEach((teste) => {

            const estudanteId = teste.estudante === undefined ? teste.id : teste.estudante.id;

            if (!testesPorEstudante[estudanteId]) {

                testesPorEstudante[estudanteId] = { ...teste, notas: {} };

            }
            try {
                const avaliacao = avaliacoes.find(
                    (a) =>
                        a.tipoAvaliacao === teste.avaliacao.tipoAvaliacao && a.ordem === teste.avaliacao.ordem

                );
                const avaliacaoId = avaliacao ? avaliacao.id : null;
                testesPorEstudante[estudanteId].notas[avaliacaoId] = teste.nota || "";

            } catch (error) {
                const avaliacao = avaliacoes.find(
                    (a) =>

                        a.tipoAvaliacao === 'Teste' && a.ordem === 1
                );
                const avaliacaoId = avaliacao ? avaliacao.id : null;
                testesPorEstudante[estudanteId].notas[avaliacaoId] = teste.nota || "";

            }

        });

        const novasFrequencias = {};
        Object.keys(testesPorEstudante).forEach((estudanteId) => {
            novasFrequencias[estudanteId] = calculateMedia(testesPorEstudante[estudanteId].notas, avaliacoesNormais);
        });
        setFrequencias(novasFrequencias);
        setNotas(Object.values(testesPorEstudante));
        setCarregando(false);


        setUpdateDom(!updateDom);

    }
    const handleNotaChange = (estudanteId, avaliacaoId, event) => {

        const newValue = event.target.value;
        if (!isNaN(newValue) && newValue >= 0 && newValue <= 20) {
            const novasNotas = notas.map((notaEstudante) => {
                const estudanteIdAUX = notaEstudante.estudante === undefined ? notaEstudante.id : notaEstudante.estudante.id;
                if (estudanteIdAUX === estudanteId) {
                    const notasModificadasCopy = [...notasModificadas];
                    const notaModificada = notasModificadasCopy.find(
                        (nota) => nota.estudanteId === estudanteId && nota.avaliacaoId === avaliacaoId
                    );

                    if (notaModificada) {
                        notaModificada.nota = newValue;
                    } else {
                        notasModificadasCopy.push({
                            estudanteId,
                            avaliacaoId,
                            nota: newValue,
                        });
                    }
                    setNotasModificadas(notasModificadasCopy);

                    const novaNotaEstudante = {
                        ...notaEstudante,
                        notas: {
                            ...notaEstudante.notas,
                            [avaliacaoId]: newValue,
                        },
                    };

                    return novaNotaEstudante;
                }
                return notaEstudante;
            });

            setNotas(novasNotas);
        }


    };

    async function handleSalvar() {

        try {
            const notasEnviadas = notasModificadas.map((notaModificada) => {
                const { estudanteId, avaliacaoId, nota } = notaModificada;
                return {
                    estudante: { id: estudanteId },
                    nota,
                    avaliacao: { id: avaliacaoId },
                    cadeira: { id: idCadeira },
                };
            });
            await Promise.resolve(requesicoes().postTeste(notasEnviadas));
            Swal.fire("Notas Lançadas com sucesso!");
        }
        catch (e) {
            Swal.fire({
                icon: 'error',
                title: 'Oops...',
                text: e.response.data.message,
                footer: 'Se o erro persistir! Porfavor entre em conatcto conosco centroinformatico@am.ac.mz'
            })

        }
    };

    const getMediaClass = (media) => {

        return media !== "" && parseFloat(media) < 9.5 ? "media-red" : "";
    };

    const calculateMedia = (notas) => {
        const novoObjeto = {};
        for (const id in notas) {
            const avaliacaoCorrespondente = avaliacoes.find(avaliacao => avaliacao.id === parseInt(id));
            if (!avaliacaoCorrespondente || (avaliacaoCorrespondente.tipoAvaliacao !== 'Exame' && avaliacaoCorrespondente.tipoAvaliacao !== 'Rec')) {
                novoObjeto[id] = notas[id];
            }
        }
        const notasArray = Object.values(novoObjeto);
        const notasValidas = notasArray.filter((nota) => nota !== "" && nota !== null && !isNaN(parseFloat(nota)));

        if (notasValidas.length >= 3) {
            const sum = notasValidas.reduce((acc, nota) => acc + parseFloat(nota), 0);
            return (sum / notasValidas.length).toFixed(2);
        }

        return "";
    };

    const calcularMediaExame = (frequenciaEstudante, notaExame, notaRecorrencia) => {
        const notaExameFloat = parseFloat(notaExame);
        const notaRecorrenciaFloat = parseFloat(notaRecorrencia);

        if (!isNaN(notaExameFloat) && isNaN(notaRecorrenciaFloat)) {
            if (notaExameFloat < 8) {
                if (notaRecorrenciaFloat) {
                    return ((parseFloat(frequenciaEstudante) * 0.6 + 0.4 * notaRecorrenciaFloat)).toFixed(0);
                }

                return (notaExameFloat)
            }
            return ((parseFloat(frequenciaEstudante) * 0.6 + 0.4 * notaExameFloat)).toFixed(0);
        }

        if (!isNaN(notaRecorrenciaFloat)) {
            return ((parseFloat(frequenciaEstudante) * 0.6 + 0.4 * notaRecorrenciaFloat)).toFixed(0);
        }

        return "";
    };

    if (redirect) {
        return <Redirect to={to} />;
    }
    if (carregando) return 'Carregando';
    return (
        <div>{estudantes !== 0 ? (<table className="AppTable">

            <thead>
                <tr>
                    <th>Codigo</th>
                    <th>Nome Completo</th>
                    {avaliacoesExame.map((a, i) => (
                        <th className="right">{a.tipoAvaliacao} </th>
                    ))}
                    <th className="right">M.Final</th>
                    <th className="right">Resultado</th>

                </tr>
            </thead>
            <tbody>

                {notas.map((notaEstudante) => {
                    const estudanteId = notaEstudante.estudante === undefined ? notaEstudante.id : notaEstudante.estudante.id;
                    const frequenciaEstudante = frequencias[estudanteId] || ""; // Obter a frequência calculada
                    //Quando nao tem notas de Exame
                    semNotasdeExames = isNaN(notaEstudante.notas[avaliacoesExame[0].id]) && isNaN(notaEstudante.notas[avaliacoesExame[1].id]) && ''
                    //Quando somente tem nota de exame
                    notadeExame = notaEstudante.notas[avaliacoesExame[0].id] && isNaN(notaEstudante.notas[avaliacoesExame[1].id]) && (calcularMediaExame(frequenciaEstudante, notaEstudante.notas[avaliacoesExame[0].id]) < 10 ? '' : parseFloat(notaEstudante.notas[avaliacoesExame[0].id]) >= 8 && calcularMediaExame(frequenciaEstudante, notaEstudante.notas[avaliacoesExame[0].id]))
                    //Quando tem nota de Recorrencia
                    notadeRecore = notaEstudante.notas[avaliacoesExame[0].id] && notaEstudante.notas[avaliacoesExame[1].id] && parseFloat(calcularMediaExame(frequenciaEstudante, notaEstudante.notas[avaliacoesExame[1].id])).toFixed(0)

                    return (
                        <tr key={notaEstudante.estudante === undefined ? notaEstudante.id : notaEstudante.estudante.id}>
                            <td>{notaEstudante.estudante === undefined ? notaEstudante.numero : notaEstudante.estudante.numero}</td>
                            <td>
                                {notaEstudante.estudante === undefined ? notaEstudante.nome : notaEstudante.estudante.nome}
                                {notaEstudante.estudante === undefined ? notaEstudante.apelido : notaEstudante.estudante.apelido}
                            </td>

                            {avaliacoesExame.map((avaliacao) => {
                                const avaliacaoId = avaliacao.id;
                                const nota =
                                    notaEstudante.notas[avaliacaoId] !== undefined
                                        ? notaEstudante.notas[avaliacaoId]
                                        : "";
                                const isNotaSet = nota !== "" && nota !== null && !isNaN(parseFloat(nota));

                                return (
                                    <td key={avaliacaoId} className="right">
                                        <TextField disabled={frequenciaEstudante < 9.5 || (notaEstudante.notas[avaliacoesExame[0].id] >= 8) && (parseFloat(frequenciaEstudante) * 0.6 + parseFloat(notaEstudante.notas[avaliacoesExame[0].id] * 0.4)).toFixed(0) >= 10 || frequenciaEstudante >= 13.5 || calcularMediaExame(frequenciaEstudante,
                                            notaEstudante.notas[avaliacoesExame[0].id], notaEstudante.notas[avaliacoesExame[1].id]
                                        ) > 10}
                                            className="imput"
                                            id={`nota-${notaEstudante.estudante == undefined ? notaEstudante.id : notaEstudante.estudante.id}-${avaliacaoId}`}
                                            value={nota}
                                            onChange={(event) => handleNotaChange(notaEstudante.estudante == undefined ? notaEstudante.id : notaEstudante.estudante.id, avaliacaoId, event)}
                                        />
                                    </td>
                                );
                            })}
                            {/* Eh preciso vericar quando o estudante tem nota de recorrencia */}
                            <td className="right">
                                {semNotasdeExames === '' && (parseFloat(frequenciaEstudante).toFixed(0) < 10 && parseFloat(frequenciaEstudante).toFixed(0) || parseFloat(frequenciaEstudante).toFixed(0) >= 14 && parseFloat(frequenciaEstudante).toFixed(0))
                                    || notadeExame  //notadeExame && "Tem nota de recorrencia" 
                                    || notadeRecore
                                }
                            </td>
                            {/* calcularMediaExame(
                                    frequenciaEstudante,
                                    notaEstudante.notas[avaliacoesExame[0].id],
                                    notaEstudante.notas[avaliacoesExame[1].id]  */}

                            <td className={`right ${getMediaClass(calculateMedia(notaEstudante.notas))}`} style={parseFloat(calcularMediaExame(
                                frequenciaEstudante,
                                notaEstudante.notas[avaliacoesExame[0].id],
                                notaEstudante.notas[avaliacoesExame[1].id]
                            )) < 9.5 ? { color: 'red' } : {}}>
                                {
                                    semNotasdeExames === '' && (parseFloat(frequenciaEstudante).toFixed(0) < 10 && 'Reprovado' || parseFloat(frequenciaEstudante).toFixed(0) >= 14 && 'Aprovado') ||
                                    notadeExame === '' && (calcularMediaExame(frequenciaEstudante, notaEstudante.notas[avaliacoesExame[0].id]) >= 10 ? 'Aprovado' : 'Recorrencia')
                                    || notadeRecore && (calcularMediaExame(frequenciaEstudante, notaEstudante.notas[avaliacoesExame[1].id]) >= 10 ? 'Aprovado' : 'Reprovado')

                                }
                            </td>

                        </tr>
                    );
                })}

            </tbody>
        </table>) : (<Alert severity="warning">Esta cadeira ainda não tem estudantes matriculados. Pode se dar caso em que ela decorre no 2º Semestre. Se tens certeza que ela esta a decorrer por favor contacte o seu departamento para regularização.</Alert>)
        }
            {estudantes !== 0 && (<Button variant="contained" color="primary" type='submit' size="large" onClick={handleSalvar}
                className="btnSalvar" startIcon={<SaveIcon />}>
                Guardar
            </Button>)}
        </div>
    )
}
export default PautaCadeira